.card {
    height: 7em;
    width: 4em;
    border: solid black 0.1em;
    border-radius: 0.5em;
    display: flex;
    margin: 1em;
    position: relative;
    padding: .1em;
}

.topCorner {
    display: flex;
    flex-direction: column;
}

.botCorner {
    display: flex;
    flex-direction: column;
    position: absolute;
    transform: rotate(180deg);
    bottom: 0;
    right: 0;
}

.center {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 2em;
}